import React, { Suspense } from "react";
import { OrbitControls, PerspectiveCamera } from "@react-three/drei";
import * as THREE from "three";
import { Canvas, useFrame, useThree } from "@react-three/fiber";
import ImageLayerItem from "./ImageLayerItem";

interface ImageLayerGroupProps {
  groupName: string,
  imageHeight: number,
  imageWidth: number,
  imageSize: number,
  imageNameList: string[],
  scaleZ: number,
}

// https://codesandbox.io/s/textureloader-rusfd?file=/src/App.js:0-1383
export default function ImageLayerGroup(props: ImageLayerGroupProps) {
  const { groupName, imageNameList, imageHeight, imageWidth, imageSize, scaleZ } = props;

  function Rig() {
    const { camera, mouse } = useThree();
    const vec = new THREE.Vector3();
    return useFrame(() => camera.position.lerp(vec.set(mouse.x * -10, mouse.y * -10, camera.position.z), 0.1))
  }

  return (
    <Canvas>
      <Suspense fallback={null}>
        <PerspectiveCamera makeDefault position={[0, 4, 14]} />
        <ambientLight />
        <directionalLight />
        {
          imageNameList.map((imageName, index) => {
            return <ImageLayerItem key={`three-${groupName}-${index}`} groupName={groupName} imageHeight={imageHeight} imageWidth={imageWidth} imageSize={imageSize} imageLayerNumber={index} imageUrl={`./asset/images/${groupName}/ar/${imageName}`}
            scaleZ={scaleZ} />
          })
        }
        <OrbitControls
          screenSpacePanning={false}
          enablePan={false}
          minDistance={6}
          maxDistance={20}
          minPolarAngle={Math.PI / 5}
          maxPolarAngle={(Math.PI * 2) / 3}
          minAzimuthAngle={-Math.PI / 5}
          maxAzimuthAngle={Math.PI / 5}
        />
      </Suspense>
      <Rig />
    </Canvas>
  );
}
