import React, { FunctionComponent as FC } from 'react';
import { useSelector } from 'react-redux'; // To call Action Creators
import { State } from '../../../../state';
import _ from 'lodash';
import './index.scss';

import { textDict } from '../../../../types/data.global';

interface AuthorUnitProps {
  imgUrl: string,
  authorName: string,
  authorOrg: string,
  homepage: string,
}

const AuthorUnit: FC<AuthorUnitProps> = (props) => {
  const { imgUrl, authorName, authorOrg, homepage } = props
  const selectedLanguage: string = useSelector((state: State) => state.uiReducer?.language) as string
  const languageVersion = textDict[selectedLanguage as ('English' | 'Chinese')]
  const authorDict = languageVersion.authorDict

  return (
    <div className='author-unit'>
      <div className='author-img'>
        <img src={imgUrl} className='author-image-class' alt={authorName}
          style={{
            WebkitMaskBoxImage: `url(${authorDict.maskImageUrl})`,
          }}
        />
      </div>
      <div className='author-name'>
        <a href={homepage} className='href-name'>{authorName}</a></div>
      <div className='author-org'>{authorOrg}</div>
    </div>
  )
}


export default AuthorUnit;