import React, { Suspense, useEffect, useState } from "react";
import "./index.scss";
import { Link } from "react-router-dom";

interface NavImageProps {
  height: number;
}

export default function NavImage(props: NavImageProps) {
  const { height } = props;
  const [hoverWorkName, setHoverWorkName] = useState<string | null>(null);

  useEffect(() => {
    console.log(hoverWorkName);
  }, [hoverWorkName]);

  const onOverWork = (name: string) => {
    setHoverWorkName(name);
  }

  return (
    <div className='nav-image-container' style={{ marginTop: "64px", height: `calc(${height} - 64px)` }}>
      <Link to="/fs" className="nav-item nav-feitian" onMouseOver={() => onOverWork("feitian")}>
        {
          hoverWorkName === "feitian" ?
            <video loop autoPlay muted>
              <source src="./asset/video/feitian.webm" type="video/webm" />
              Your browser does not support the video tag.
            </video> :
            <img src="./asset/images/workone/.png" />
        }
      </Link>
      <Link to="tm" className="nav-item nav-lanyue" onMouseOver={() => onOverWork("lanyue")}>
        {
          hoverWorkName === "lanyue" ?
            <video loop autoPlay muted>
              <source src="./asset/video/lanyue.webm" type="video/webm" />
              Your browser does not support the video tag.
            </video> :
            <img src="./asset/images/worktwo/.png" />
        }
      </Link>

      <Link to="gs" className="nav-item nav-juxing" onMouseOver={() => onOverWork("juxing")}>
        {
          hoverWorkName === "juxing" ?
            <video loop autoPlay muted>
              <source src="./asset/video/juxing.webm" type="video/webm" />
              Your browser does not support the video tag.
            </video> :
            <img src="./asset/images/workthree/.png" />
        }
      </Link>
    </div>
  );
}
