import React, { FunctionComponent as FC } from 'react';
import { useSelector } from 'react-redux'; // To call Action Creators
import { State } from '../../../state';
import _ from 'lodash';
import './index.scss';

import { textDict } from '../../../types/data.global';
import AuthorUnit from './AuthorUnit';

interface AuthorpageProps {
  height: number,
}

const Authorpage: FC<AuthorpageProps> = (props) => {
  const { height } = props
  const selectedLanguage: string = useSelector((state: State) => state.uiReducer?.language) as string
  const languageVersion = textDict[selectedLanguage as ('English' | 'Chinese')]
  const authorDict = languageVersion.authorDict

  const studentAuthorList: JSX.Element[] = authorDict.studentList.map((student, index) => (
    <AuthorUnit key={`studentAuthor-${index}`} imgUrl={student.photoUrl} authorName={student.name} authorOrg={student.orgShort} homepage={student.homepage}/>
  ))

  const professorAuthorList: JSX.Element[] = authorDict.professorList.map((professor, index) => (
    <AuthorUnit key={`professorAuthor-${index}`} imgUrl={professor.photoUrl} authorName={professor.name} authorOrg={professor.orgShort} homepage={professor.homepage}/>
  ))

  return (
    <div className='author-page'>
      <div className='title'>{languageVersion.authorDict.title}</div>
      <div className='student-group'>{studentAuthorList}</div>
      <div className='professor-group'>{professorAuthorList}</div>
    </div>
  )
}


export default Authorpage;