import { useEffect, useRef } from "react";
import * as THREE from "three";
import { useAspect, useTexture } from "@react-three/drei";
import { useFrame, useThree } from "@react-three/fiber";

interface ImageLayerItemProps {
  groupName: string,
  imageUrl: string,
  imageHeight: number,
  imageWidth: number,
  imageSize: number,
  imageLayerNumber: number,
  scaleZ: number,
}

// https://codesandbox.io/s/textureloader-rusfd?file=/src/App.js:0-1383
export default function ImageLayerItem(props: ImageLayerItemProps) {
  const { groupName, imageUrl, imageHeight, imageWidth, imageSize, imageLayerNumber, scaleZ } = props;
  const [img] = useTexture([imageUrl]);
  const size = useAspect(imageWidth, imageHeight, imageSize);
  const ref = useRef();

  return (
    <>
      <mesh scale={size} position={[0, 0, imageLayerNumber * scaleZ]}>
        <planeBufferGeometry />
        <meshStandardMaterial
          transparent={true}
          toneMapped={false}
          side={THREE.DoubleSide}
          displacementScale={0.2}
          map={img}
        />
      </mesh>
    </>
  );
}
