import React, { FunctionComponent as FC } from 'react';
import _ from 'lodash';
import { Link } from "react-router-dom";
import './index.scss';


interface WorkpageProps {
  height: number,
  title: string,
  subtitle: string,
  intro: string[],
  titleColor: string,
  imgUrl: string,
  route: string,
}

const Workpage: FC<WorkpageProps> = (props) => {
  const { height, title, subtitle, intro, titleColor, imgUrl, route } = props

  const introGroup: JSX.Element[] = intro.map((paragraph: string, index: number) => (
    <div className='paragraph' key={`paragraph-${index}`} style={{fontFamily: 'alibaba-puhuiti-light'}}>{paragraph}</div>
  ))

  return (
    <div className='work-page' style={{height: height}}>
      <div className='text-intro'>
        <Link to={route}><div className='title' style={{color: titleColor}}>{title}</div></Link>
        <div className='subtitle'>{subtitle}</div>
        {introGroup}
      </div>
      <div className='image-group'>
      <Link to={route}><img className='image' src={imgUrl} alt={title} /></Link>
      </div>
    </div>
  )
}


export default Workpage;